import React from "react";
import "../dist/css/main.css";

var Tile = (obj) => {
    return (
        <div className="tile__container">
            <div className="tile__image">
                <img src={require("../dist/images/" + obj.tile.image + ".png")} className="tile__image--size" alt="" />
            </div>
            <div className="tile__upper-box">
                <p className="tile__headline">{obj.tile.headline}</p>
            </div>
            <div className="tile__lower-box">
                <p className="tile__sub-headline">{obj.tile.subheadline}</p>
            </div>
        </div>
    );
};

export default Tile;