import React from "react";
import "../dist/css/main.css";
import { Link } from "react-router-dom";

const linkArray = [
    { path: "/", linkText: "About Sewcase" },
    { path: "/convention", linkText: "Convention" },
    { path: "/donate", linkText: "Donate" },
    { path: "/organizers", linkText: "Organizers" },
    { path: "/faq", linkText: "FAQ" }
];

function Header() {
    return (
        <nav className="header">
            <div>
                <h2 className="header__info-text">
                    Hier könnte noch ein ziemlich schnieker Text stehen
                </h2>
                <div>
                    
                </div>
                <div onClick={toggleNavBar} className="header__button">
                    <p>Menü</p>
                </div>
                <section id="header-section" className="header__section">
                    <ul className="header__upper-list">
                        {linkArray.map(element => {
                            return (
                                <li>
                                    <Link
                                        onClick={hideNavBar}
                                        to={element.path}
                                        className="header__link"
                                    >
                                        {element.linkText}
                                    </Link>
                                </li>
                            );
                        })}
                    </ul>
                </section>
            </div>
        </nav>
    );
}

var toggleNavBar = () => {
    document
        .getElementById("header-section")
        .classList.toggle("header__section--active");
};
var hideNavBar = () => {
    document
        .getElementById("header-section")
        .classList.remove("header__section--active");
};

export default Header;
